import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";

import Loader from "react-loader-spinner";
import SocketClient from "../../utils/socketClient";
import Timer from "../timer";
// import "./style.css";
const Config = require("../../utils/config");
const SingleValidate = () => {
  const dispatch = useDispatch();
  const [email, setEamil] = useState("");
  const [response, setResponse] = useState({});
  useEffect(() => {}, []);
  const singleValidation = () => {
    fetch(
      `https://api.zerobounce.net/v2/validate?api_key=${Config.CONFIG.ZEROBOUNCE_KEY}&email=${email}&ip_address=`,
      {
        method: "GET",
        mode: "cors",
      }
    )
      .then((r) => r.json())
      .then((data) => {
        console.log("single validate ", data);
        setResponse(data);
        // Object.keys(data).map((key, value) => (
        //   console.log("object value", key, data[key])
        // ))
        // setFilters((state) => [{ id: 0, name: "All Contacts" }, ...state]);
        // collectEmails(data.data);
      });
  };
  const socket = useSelector((state) => state.socket);

  // console.log("socket finish catch", socket);
  return (
    <div className="mt-4">
      <h3>Single Email Validation</h3>
      <div className="box">
        {/* <label for="email">Email Address:</label> */}
        <p style={{ fontSize: "15px" }}>Enter an email address to validate</p>
        <input
          className="input"
          id="email"
          onChange={(e) => {
            setEamil(e.target.value);
          }}
        />
      </div>
      <div className="box">
        <button className="button" onClick={singleValidation}>
          <i className="fa fa-search mr-3"></i>Single Email Validation
        </button>
      </div>
      {response ? (
        <div className="mt-4">
          {Object.keys(response).map((key, value) => (
            <div >
              {response[key] == null ? (
                ""
              ) : (
                <p key={value} style={{ fontSize: "17px" }}>
                  <span style={{textDecoration:'underline'}}>{key}:</span>
                  <span style={{color:'green'}}>&nbsp;{response[key]}</span>
                </p>
              )}
            </div>
          ))}
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

export default SingleValidate;
