import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import SocketClient from "../../utils/socketClient";

const Operation = ({ openBulkPage, openSinglePage, handleCredit }) => {
  const dispatch = useDispatch();
  const [amount, setAmount] = useState(1000);

  useEffect(() => {}, []);
  const socket = useSelector((state) => state.socket);

  const logout = () => {
    localStorage.removeItem("loggedIn");
    localStorage.removeItem("user");
    localStorage.removeItem("access_token");
    localStorage.removeItem("status");
    window.location.reload();
  };

  return (
    <div>
      <div>
        {/* <div className="mt-4 box">
          <a onClick={logout}>
            <button className="logoutButton" onClick={logout}>
              LOGOUT
            </button>
          </a>
        </div> */}
        <div className="mt-4 box">
          <b>Pipedrive User: </b>
          <p>{socket.user.name}</p>
          <p>{socket.user.email}</p>
          <p>{socket.user.company_name}</p>
          <p>
            Credits:{" "}
            {SocketClient.getInstance().numberWithCommas(socket.credits)}
          </p>
        </div>
      </div>
      <div className="mt-4 box">
        <a>
          <button className="sectionButton"></button>
        </a>
        <a>
          <button className="sectionButton" onClick={openBulkPage}>
            Bulk Email Validation
          </button>
        </a>
        <a>
          <button className="sectionButton" onClick={openSinglePage}>
            Single Email Validation
          </button>
        </a>
        <a>
          <button className="sectionButton" onClick={handleCredit}>
            Purchase Credits
          </button>
        </a>
        {/* <a onClick={logout}> 
          <button className="sectionButton" onClick={handleCredit}>Logout</button>
        </a> */}
      </div>
    </div>
  );
};

export default Operation;
